.login {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/bg.61b864b0.jpg);
  background-color: lightgray;
  background-size: 100% 100%;
}
.login .login-header {
  height: 50px;
  display: flex;
  background: rgba(255, 255, 255, 0.2);
}
.login .login-header img {
  width: 50px;
  height: 50px;
  margin: 0 0 0 80px;
}
.login .login-header img:hover {
  cursor: pointer;
}
.login .login-header h3 {
  font-size: 25px;
  line-height: 50px;
  color: white;
}
.login .login-header h3:hover {
  cursor: pointer;
}
.login .login-content {
  width: 400px;
  height: 300px;
  background: white;
  margin: 50px auto;
  padding: 20px 40px;
  box-shadow: 5px 5px 10px gray;
}
.login .login-content h2 {
  font-size: 25px;
  text-align: center;
  margin-bottom: 20px;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
}
.login .login-content .login-form .login-form-forgot {
  float: right;
}
